body {
  cursor: none; /* Hide the default cursor */
}

.cursor,
.cursor-follower {
  pointer-events: none;
  position: fixed;
  will-change: transform;
  z-index: 9999;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  transition: width 0.2s, height 0.2s, background-color 0.3s, transform 0.2s;
}

/* Main cursor dot */
.cursor {
  width: 10px;
  height: 10px;
  transition-duration: 0.1s;
}

/* Cursor follower ring */
.cursor-follower {
  width: 30px;
  height: 30px;
  border: 2px solid;
  transition-duration: 0.15s;
  transition-delay: 0.03s;
  opacity: 0.7;
  background-color: transparent;
}

/* Light theme styles */
.cursor.cursor--light {
  background: #000;
  box-shadow: 0 0 16px 4px rgba(0,0,0,0.3), 0 0 2px 1px #000;
}

.cursor-follower.cursor--light {
  border: 2px solid #000;
  opacity: 0.5;
}

/* Dark theme styles */
.cursor.cursor--dark {
  background: #fff;
  box-shadow: 0 0 16px 4px rgba(255,255,255,0.5), 0 0 2px 1px #fff;
}

.cursor-follower.cursor--dark {
  border: 2px solid #fff;
  opacity: 0.18;
}

/* White theme styles */
.cursor.cursor--white {
  width: 12px;
  height: 12px;
  background: #fff;
  box-shadow: 0 0 16px 4px rgba(255, 255, 255, 0.5), 0 0 2px 1px #fff;
  transition-duration: 0.1s;
}

.cursor-follower.cursor--white {
  width: 36px;
  height: 36px;
  border: 2px solid #fff;
  opacity: 0.18;
  background: transparent;
  transition-duration: 0.15s;
  transition-delay: 0.03s;
}

/* Меняем стиль для светлой темы */
.cursor.cursor--white.cursor--light-theme {
  width: 12px;
  height: 12px;
  background: #000; /* Черный на светлой теме */
  box-shadow: 0 0 16px 4px rgba(0,0,0,0.5), 0 0 2px 1px #000;
}

.cursor-follower.cursor--white.cursor--light-theme {
  width: 36px;
  height: 36px;
  border: 2px solid #000; /* Черная граница на светлой теме */
  opacity: 0.18;
}

/* Для темной темы оставляем белый как есть */
.cursor.cursor--white.cursor--dark-theme {
  background: #fff;
  box-shadow: 0 0 16px 4px rgba(255,255,255,0.5), 0 0 2px 1px #fff;
}

.cursor-follower.cursor--white.cursor--dark-theme {
  border: 2px solid #fff;
}

/* Hidden state */
.cursor--hidden {
  opacity: 0;
}

/* Click animation */
.cursor--clicked {
  transform: translate(-50%, -50%) scale(0.7);
  background: #ec704c !important;
  box-shadow: 0 0 8px 2px #ec704c;
}

.cursor-follower--clicked {
  transform: translate(-50%, -50%) scale(1.5);
  opacity: 0;
  border-color: #ec704c !important;
}

/* Hover state for interactive elements */
.cursor--hover {
  width: 22px;
  height: 22px;
}

.cursor-follower--hover {
  width: 48px;
  height: 48px;
  border-width: 3px;
  opacity: 0.3;
}

@media (max-width: 768px), (pointer: coarse) {
  .cursor, .cursor-follower {
    display: none; /* Disable on touch devices */
  }
  
  body {
    cursor: auto; /* Restore default cursor */
  }
}
