* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}
::-moz-selection {
  background: #00bfbf;
  color: #fafafa;
  text-shadow: none;
}
::selection {
  background: #00bfbf;
  color: #fafafa;
  text-shadow: none;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
html {
  overflow: scroll;
  overflow-x: hidden;
  font-size: 16px;
  scroll-behavior: smooth;
}
body {
  transition: all 0.5s ease;
  font-weight: 500; /* Увеличиваем базовый вес шрифта */
}
p {
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
}
p, span, h1, h2, h3, h4, h5, h6, button, a, li, div {
  font-weight: 600 !important; /* Принудительно увеличиваем вес шрифта */
}