nav {
  position: fixed;
  display: flex;
  flex-direction: row-reverse;
  transform: rotate(-90deg) translate3d(-50%,0,0);
  top: 15%;
  left: -6.5rem;
}
nav a {
  margin-right: 24px; /* было 20px */
  text-decoration: none;
  color: #575757;
  font-weight: 600;
  font-size: 1.32rem; /* было 1.1rem */
  padding: 0.7em 1.2em; /* добавлено для увеличения кликабельной области */
  border-radius: 8px;
  transition: color 0.3s, background 0.3s;
}
nav button {
  background: none;
  border: none;
  cursor: pointer;
  margin-right: 24px;
  text-decoration: none;
  color: inherit;
  font-weight: 600;
  font-size: 1.32rem;
  padding: 0.7em 1.2em;
  border-radius: 8px;
  transition: color 0.3s, background 0.3s;
}
nav button.active,
nav button:hover {
  /* Цвета для активной/hover кнопки уже определяются через .side-navbar.light/.dark a.active, можно скопировать стили */
  color: var(--nav-active, #00bfff);
  background: rgba(0,0,0,0.07);
}
nav a:hover {
  color: var(--nav-hover);
}
nav a.active {
  color: var(--nav-active);
}

/* Light theme */
.side-navbar.light a {
  color: #3a6ea5;
}
.side-navbar.light a.active,
.side-navbar.light a:hover {
  color: #00bfff;
}

/* Dark theme */
.side-navbar.dark a {
  color: #b3aaff;
}
.side-navbar.dark a.active,
.side-navbar.dark a:hover {
  color: #a259ff;
}

.side-navbar {
  position: fixed;
  display: flex;
  flex-direction: row-reverse;
  transform: rotate(-90deg) translate3d(-50%, 0, 0);
  top: 25%;
  left: -6.5rem;
  z-index: 1000; /* Убедимся, что z-index ниже, чем у логотипа (1100) */
}

.side-navbar button {
  background: none;
  border: none;
  cursor: pointer;
  margin-right: 20px;
  text-decoration: none;
  color: #575757;
  font-weight: 600;
  font-size: 1rem;
  padding: 0;
  transition: color 0.3s;
  outline: none;
}

.side-navbar button:hover {
  margin-right: 20px;
  text-decoration: none;
  color: #ec704c;
}

.side-navbar button.active {
  color: #ec704c;
}

/* Адаптация для тем */
.side-navbar.light button {
  color: #575757;
}
.side-navbar.light button:hover,
.side-navbar.light button.active {
  color: #ec704c;
}

.side-navbar.dark button {
  color: #b3aaff;
}
.side-navbar.dark button:hover,
.side-navbar.dark button.active {
  color: #ec704c;
}

/* Мобильная версия - сохраняем тот же стиль */
@media (max-width: 768px) {
  .side-navbar {
    /* Сохраняем ту же трансформацию */
    transform: rotate(-90deg) translate3d(-50%, 0, 0);
    top: 30%; /* Немного ниже на мобильных */
    left: -7rem; /* Корректируем позицию */
  }
  
  .side-navbar button {
    font-size: 0.9rem;
    margin-right: 15px;
  }
}

@media (max-width: 480px) {
  .side-navbar {
    top: 35%;
    left: -7.5rem;
  }
  
  .side-navbar button {
    font-size: 0.8rem;
    margin-right: 12px;
  }
}